<template>
  <div class="cropping" @mousemove="mouseMove" @mouseup="mouseUp">
    <!--  -->
    <div class="btn-line" v-if="!hideBtn">
      <button class="rel" type="primary" v-if="setShow.choose">
        <input class="lucency" type="file" ref="inputer" @input="change" />
        
      </button>
      <button v-if="setShow.cropping" type="primary" @click="croppingImage">
        
      </button>
      <button v-if="setShow.export" type="primary" @click="getImage">
        
      </button>
    </div>
    <!--  -->
    <div class="crop-image" :style="{ width: width, height: height }">
      <div class="crop-image-content" v-if="fileUrl">
        <!--    -->
        <div class="crop-box" ref="cropBox">
          <!--   -->
          <img
            ref="cropimg1"
            class="cropimg1"
            :src="fileUrl"
            @load="setPosInfo"
          />
          <!--  -->
          <img
            class="cropimg2"
            :src="fileUrl"
            :style="{ clip: clip }"
            ref="cropimg2"
          />
          <!--    -->
          <div
            class="main-box"
            :style="{
              width: mainBoxSize.width + 'px',
              height: mainBoxSize.height + 'px',
              top: mainBoxSize.top + 'px',
              left: mainBoxSize.left + 'px',
            }"
            ref="mainBox"
            @mousedown.stop="mouseDown($event, 'mainBox')"
          >
            <div
              id="left-up"
              class="min-box left-up"
              @mousedown.stop="mouseDown($event, 'left-up')"
            ></div>
            <div
              id="up"
              class="min-box up"
              @mousedown.stop="mouseDown($event, 'up')"
            ></div>
            <div
              id="right-up"
              class="min-box right-up"
              @mousedown.stop="mouseDown($event, 'right-up')"
            ></div>
            <div
              id="left"
              class="min-box left"
              @mousedown.stop="mouseDown($event, 'left')"
            ></div>
            <div
              id="right"
              class="min-box right"
              @mousedown.stop="mouseDown($event, 'right')"
            ></div>
            <div
              id="left-down"
              class="min-box left-down"
              @mousedown.stop="mouseDown($event, 'left-down')"
            ></div>
            <div
              id="down"
              class="min-box down"
              @mousedown.stop="mouseDown($event, 'down')"
            ></div>
            <div
              id="right-down"
              class="min-box right-down"
              @mousedown.stop="mouseDown($event, 'right-down')"
            ></div>
          </div>
        </div>

        <!--    -->
        <!-- divhtml2canvascanvas -->
        <div class="hide-preview">
          <div ref="preview">
            <img class="cropimg3" ref="cropimg3" alt="" :src="fileUrl" />
          </div>
        </div>
        <!-- canvas -->
        <div ref="showPreview" class="preview-view"></div>
        <!--  end  -->
      </div>
    </div>
  </div>
</template>
<script>
import html2canvas from "html2canvas";
export default {
  props: {
    hideBtn: {
      type: Boolean,
      default: false,
    },
    btns: {
      type: [Object, Array],
      default: () => {
        return ["choose", "cropping", "export"];
      },
    },
    width: {
      type: String,
      default: "100%",
    },
    height: {
      type: String,
      default: "auto",
    },
  },
  computed: {
    // 
    clip: function() {
      let { rect } = this;
      return `rect(${rect.start}px, ${rect.x}px, ${rect.y}px, ${rect.end}px)`;
    },
    setShow: function() {
      let res = {};
      this.btns.forEach((vo) => {
        res[vo] = 1;
      });
      return res;
    },
  },
  data() {
    return {
      fileUrl: "",
      file: "",
      show: false,
      ifKeyDown: false, // 
      contact: "", // ,
      posInfo: {},
      rect: {
        start: 0,
        x: 0,
        y: 0,
        end: 0,
      },
      // 
      mainBoxSize: {
        width: "150",
        height: "150",
        left: "0",
        top: "0",
      },
      startPoint: { x: 0, y: 0 },
    };
  },
  mounted() {
    // this.setPosInfo()
  },
  methods: {
    change() {
      let files = this.$refs.inputer.files; //el-input files
      // let url = window.URL || window.webkitURL
      // let src = url.createObjectURL(files[0]) // File

      // filebase64 html2canvas  base64
      this.setImage(files[0])
    },
    setImage(arg) {
      let that = this;
      // 
      switch(typeof arg) {
        case 'string':
          if(arg.indexOf('data:image/jpg;base64,')){
              that.fileUrl = arg
          } else {
            this.getCanvasBase64(arg).then(base64 => {
              that.fileUrl = base64
            })
          }

          break;
        case 'object':
          if(arg instanceof File) {
            var reader = new FileReader()
            reader.readAsDataURL(arg)
            reader.onload = function() {
              that.fileUrl = this.result
            }
          } else{
            console.log('')
          }
          break
        default:
          console.log('')
      }
      
    },
    // 
    setPosInfo() {
      let cropimgElem = this.$refs.cropimg1;
      var cropimgElemWidth = cropimgElem.clientWidth;
      var cropimgElemHeight = cropimgElem.clientHeight;
      var cropimgElemPosition = this.getPosition(cropimgElem);
      var minX = cropimgElemPosition.left; // x
      var maxX = cropimgElemPosition.left + cropimgElemWidth; // x
      var minY = cropimgElemPosition.top; // y
      var maxY = cropimgElemPosition.top + cropimgElemHeight; // y

      this.posInfo = {
        minX,
        maxX,
        minY,
        maxY,
      };

      this.setChoice();
      this.show = true;
    },
    // 
    setChoice() {
      var mainBoxElem = this.$refs.mainBox;
      this.rect = {
        start: mainBoxElem.offsetTop,
        x: mainBoxElem.offsetLeft + mainBoxElem.offsetWidth,
        y: mainBoxElem.offsetTop + mainBoxElem.offsetHeight,
        end: mainBoxElem.offsetLeft,
      };
    },
    mouseDown(e, key) {
      // 
      this.ifKeyDown = true;
      this.contact = key;
      this.startPoint = {
        x: e.clientX,
        y: e.clientY,
        left: this.mainBoxSize.left,
        top: this.mainBoxSize.top,
      };
    },
    mouseMove(e) {
      if (this.ifKeyDown) {
        switch (this.contact) {
          case "mainBox":
            this.mainMove(e);
            break;
          case "right":
            this.rightMove(e);
            break;
          case "up":
            this.upMove(e);
            break;
          case "left":
            this.leftMove(e);
            break;
          case "down":
            this.downMove(e);
            break;
          case "left-up":
            this.leftMove(e);
            this.upMove(e);
            break;
          case "right-up":
            this.rightMove(e);
            this.upMove(e);
            break;
          case "left-down":
            this.leftMove(e);
            this.downMove(e);
            break;
          case "right-down":
            this.rightMove(e);
            this.downMove(e);
            break;
          default:
            break;
        }
        this.setChoice();
      }
    },
    mouseUp() {
      this.ifKeyDown = false;
      this.contact = "";
    },
    getPosition(node) {
      var left = node.offsetLeft;
      var top = node.offsetTop;
      var parent = node.offsetParent;
      while (parent != null) {
        left += parent.offsetLeft;
        top += parent.offsetTop;
        parent = parent.offsetParent;
      }
      return { left: left, top: top };
    },
    mainMove(e) {
      let { startPoint, posInfo, mainBoxSize } = this;
      // left top
      let left = e.clientX - startPoint.x + parseFloat(startPoint.left); // x
      let top = e.clientY - startPoint.y + parseFloat(startPoint.top); // x
      // left top
      let maxLeft = posInfo.maxX - posInfo.minX - parseFloat(mainBoxSize.width);
      let maxTop = posInfo.maxY - posInfo.minY - parseFloat(mainBoxSize.height);
      // 
      if (left >= 0 && left < maxLeft) {
        this.mainBoxSize.left = left;
      }
      if (top >= 0 && top < maxTop) {
        this.mainBoxSize.top = top;
      }
    },
    // 
    rightMove(e) {
      var x = e.clientX; // x
      if (x > this.posInfo.maxX || x < this.posInfo.minX) {
        return;
      }
      var mainBoxElem = this.$refs.mainBox; // 
      var widthBefore = mainBoxElem.offsetWidth - 2; // 
      var addWidth = x - this.getPosition(mainBoxElem).left - widthBefore; // 
      var width = widthBefore + addWidth;
      if (width < 1) {
        return;
      }
      // mainBoxElem.style.width = width + 'px' // 
      this.mainBoxSize.width = width; // 
    },
    // 
    upMove(e) {
      /**
       * mainDom.scrollTopdiv
       * clientY
       * posInfo.maxYcropimg1
       */
      let mainDom = document.body;
      var y = e.clientY + mainDom.scrollTop; // y
      if (y > this.posInfo.maxY || y < this.posInfo.minY) {
        return;
      }
      var mainBoxElem = this.$refs.mainBox; // 
      var mainY = this.getPosition(mainBoxElem).top; // 
      var addHeight = mainY - y; // 
      var heightBefore = mainBoxElem.offsetHeight - 2; // 
      var height = heightBefore + addHeight;
      if (height < 1) {
        return;
      }
      this.mainBoxSize.height = height; // 
      this.mainBoxSize.top = mainBoxElem.offsetTop - addHeight; // 
    },
    // 
    leftMove(e) {
      var x = e.clientX; // x
      if (x > this.posInfo.maxX || x < this.posInfo.minX) {
        return;
      }
      var mainBoxElem = this.$refs.mainBox; // 
      var mainX = this.getPosition(mainBoxElem).left;
      var widthBefore = mainBoxElem.offsetWidth - 2; // 
      var addWidth = mainX - x; // 
      var width = widthBefore + addWidth;
      if (width < 1) {
        return;
      }
      this.mainBoxSize.width = width; // 
      this.mainBoxSize.left = mainBoxElem.offsetLeft - addWidth; // 
    },
    // 
    downMove(e) {
      /**
       * mainDom.scrollTopdiv
       * clientY
       * posInfo.maxYcropimg1
       */
      let mainDom = document.body;
      var y = e.clientY + mainDom.scrollTop; // y
      if (y > this.posInfo.maxY || y < this.posInfo.minY) {
        return;
      }
      var mainBoxElem = this.$refs.mainBox; // 
      var heightBefore = mainBoxElem.offsetHeight - 2; // 
      var mainY = this.getPosition(mainBoxElem).top; // 
      var addHeight = y - heightBefore - mainY; // 
      var height = heightBefore + addHeight;
      if (height < 1) {
        return;
      }
      this.mainBoxSize.height = height; // 
    },
    croppingImage() {
      this.setPreview();
    },
    setPreview() {
      let bodyTop = document.documentElement.scrollTop;
      // let cropimgElem = this.$refs.cropimg1;

      var mainBoxElem = this.$refs.mainBox; // 
      var top = mainBoxElem.offsetTop + bodyTop;
      var right = mainBoxElem.offsetLeft + mainBoxElem.offsetWidth;
      var bottom = mainBoxElem.offsetTop + mainBoxElem.offsetHeight;
      var left = mainBoxElem.offsetLeft;

      //  preview html2canvas
      var preview = this.$refs.preview;
      preview.style.width = mainBoxElem.offsetWidth + "px";
      preview.style.height = mainBoxElem.offsetHeight + "px";

      //  preview 
      var previewImg = this.$refs.cropimg3;
      // 
      let cropimgElem = this.$refs.cropimg1;
      previewImg.style.width = cropimgElem.clientWidth + "px";
      previewImg.style.height = cropimgElem.clientHeight + "px";
      // 
      previewImg.style.top = -top + "px";
      previewImg.style.left = -left + "px";
      previewImg.style.clip =
        "rect(" +
        top +
        "px, " +
        right +
        "px, " +
        bottom +
        "px, " +
        left +
        "px)";

      // html2canvas 
      html2canvas(preview).then((canvas) => {
        this.$refs.showPreview.innerHTML = "";
        // this.$refs.showPreview.appendChild(canvas);
        let oImg = new Image();
        oImg.src = canvas.toDataURL(); // 
        this.$refs.showPreview.appendChild(oImg); // body
        // var resultLink = document.createElement("a");
        // resultLink.download = `file_${new Date().getTime()}`;
        // resultLink.href = oImg.src;
        // document.body.appendChild(resultLink);
        // resultLink.click();
        // resultLink.remove();

        this._croppingCanvas = canvas;
        this.getCutImage().then((res) => {
          this.$emit("onCropping", res);
        });
      });
    },
    getCutImage() {
      let { _croppingCanvas } = this;
      return new Promise((resolve) => {
        let base64 = _croppingCanvas.toDataURL();
        let file = this.base64ToFile(base64, `file_${new Date().getTime()}`);
        resolve({ base64, file });
      });
    },
    // base64 file
    base64ToFile(base64, fileName) {
      let arr = base64.split(","),
        type = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], fileName, { type });
    },
    exportImg() {
      let { _croppingCanvas } = this;
      var url = _croppingCanvas.toDataURL();
      var resultLink = document.createElement("a");
      resultLink.download = `file_${new Date().getTime()}`;
      resultLink.href = url;
      document.body.appendChild(resultLink);
      resultLink.click();
      resultLink.remove();
    },
    getImage() {
      this.exportImg();
    },
    getCanvasBase64(src) {
      let that = this
      return new Promise((resolve, reject) => {
        var image = new Image();
        //
        image.crossOrigin = ""; // image.crossOrigin = '';
        image.src = src;

        if (src) {
          image.onload = function() {
            resolve(that.getBase64Image(image)); //base64done
            //document.getElementById("container2").appendChild(image);
          }
        } else {
          reject('')
        }
      })
      
    },
    getBase64Image(img, width, height) {
      var canvas = document.createElement("canvas");
      canvas.width = width ? width : img.width;
      canvas.height = height ? height : img.height;
      var ctx = canvas.getContext("2d");
      ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
      var dataURL = canvas.toDataURL();
      return dataURL;
    }
  }
};
</script>

<style lang="scss" scoped>
.rel {
  position: relative;
  overflow: hidden;
}

.btn-line {
  padding: 20px 0;
  text-align: left;

  button {
    margin-right: 10px;
  }
  .lucency {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    opacity: 0;
    overflow: hidden;
  }
}

.hide-preview {
  position: fixed;
  left: 0px;
  top: 0px;
  opacity: 0;
  pointer-events: none;
}
.crop-image {
  user-select: none;
  // border: 1px solid #dedede;
  border-radius: 6px;
  height: 100%;
  width: 100%;
  background: #f2f2f2;
}
/*  */
.crop-image-content {
  position: relative;
  width: 100%;
  margin-top: 10px;
  text-align: left;
}

.crop-box {
  position: relative;
  display: inline-block;
  width: 49.5%;
}

.crop-image-content .cropimg1 {
  opacity: 0.5;
  // position: absolute;
  max-width: 100%;
  max-height: 100%;
  top: 0;
  left: 0;
  vertical-align: bottom;
}

.crop-image-content .cropimg2 {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 100%;
  max-height: 100%;
  // clip: rect(0, 150px, 150px, 0);
}
/*  */
.hide-preview {
  opacity: 0;
  // pointer-events: none;
  position: absolute;
  top: 0;
  max-width: 100%;
  max-height: 100%;
}
.hide-preview .cropimg3 {
  position: absolute;
  top: 0;
  left: 0;
}

.crop-image-content .main-box {
  border: 1px solid white;
  position: absolute;
  // top: 0;
  // left: 0;
  cursor: move;
}

.min-box {
  position: absolute;
  height: 8px;
  width: 8px;
  background-color: white;
}

.left-up {
  top: -4px;
  left: -4px;
  cursor: nw-resize;
}

.up {
  left: 50%;
  margin-left: -4px;
  top: -4px;
  cursor: n-resize;
}

.right-up {
  right: -4px;
  top: -4px;
  cursor: ne-resize;
}

.left {
  top: 50%;
  margin-top: -4px;
  left: -4px;
  cursor: w-resize;
}

.right {
  top: 50%;
  margin-top: -4px;
  right: -4px;
  cursor: w-resize;
}

.left-down {
  bottom: -4px;
  left: -4px;
  cursor: sw-resize;
}

.down {
  bottom: -4px;
  left: 50%;
  margin-left: -4px;
  cursor: s-resize;
}

.right-down {
  bottom: -4px;
  right: -4px;
  cursor: se-resize;
}

.preview-view {
  position: absolute;
  left: 50.5%;
  text-align: center;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
}
</style>
